import FloatAnim from "../components/FloatAnim.js"

var sceneConfig = {
  key: 'menuScene',
  pack: {
    files: [{
      type: 'plugin',
      key: 'rexwebfontloaderplugin',
      url: 'https://raw.githubusercontent.com/rexrainbow/phaser3-rex-notes/master/dist/rexwebfontloaderplugin.min.js',
      start: true
    }] 
  }
};

class Menu extends Phaser.Scene {
  constructor() {
    super(sceneConfig);
  }

  init(data) {
     // background
     this.logo = this.add.image(400,300,'logo');
     this.cameras.main.setBackgroundColor('#2c97b3');

  }

  preload() {
    this.plugins.get('rexwebfontloaderplugin').addToScene(this);

    var config = {
      google: {
        families: ['Gloria Hallelujah', 'Source Sans Pro:300']
      }
    }
    this.load.rexWebFont(config);
    this.load.plugin('rexdropshadowpipelineplugin', 'https://raw.githubusercontent.com/rexrainbow/phaser3-rex-notes/master/dist/rexdropshadowpipelineplugin.min.js', true); 
   }

  create(data) {
    this.difficulty = 'null';
    this.logo.destroy();
    this.graphics = this.add.graphics().setDepth(3);
    this.pencil = this.add.image(95,140,"pencil").setScale(0.15)
    .setFlipX(false)
    .setDepth(5)
    .setVisible(false);
    this.pencil2 = this.add.image(95,140,"pencil").setScale(0.1)
    .setFlipX(false)
    .setDepth(6)
    .setVisible(false);
    //this.cameras.main.fadeIn(1000,44,151,179);
    this.addFloatingGadgets();
    this.strikes = this.add.container();
    //Add Title TODO: fancy it up
    this.add.text(180, 0, `Mini Networks`, { fontFamily: 'Gloria Hallelujah ', fill: '#fff', fontSize: '64px', shadow: {
      color: '#000', fill:true, offsetY: 5, offsetX: 5
    }, padding: {right: 10}});
    this.startDrawing = false;
    this.startDrawing2 = false;
    // Add level menu buttons.

    const d_easy = this.add.text(
      250,
      130,
      'Easy',
      {
        fontFamily: 'Gloria Hallelujah ',
        fontSize: '20px',
        fill: '#fff',
        shadow: {
          color: '#000', fill:true, offsetY:2, offsetX: 2
        },
        padding: {right: 10}
      }).setInteractive()
    .on('pointerdown', () => {
        this.difficulty = 'easy';
        this.pencilAnim(d_easy,this.pencil2);
        this.exitMenu('freeplayScene',500);
    })
    .on('pointerover', () => {
      this.pencil2.setVisible(true);
      this.pencil2.setPosition(d_easy.x + 5,d_easy.y - 5);
    }).setVisible(false)
    .on('pointerout', () => this.pencil2.setVisible(false));

    const d_medium = this.add.text(
      250,
      160,
      'Medium',
      {
        fontFamily: 'Gloria Hallelujah ',
        fontSize: '20px',
        fill: '#fff',
        shadow: {
          color: '#000', fill:true, offsetY:2, offsetX: 2
        },
        padding: {right: 10, up: 5}}).setInteractive()
    .on('pointerdown', () => {
        this.difficulty = 'medium';
        this.pencilAnim(d_medium,this.pencil2);
        this.exitMenu('freeplayScene',500);
    })
    .on('pointerover', () => {
      this.pencil2.setVisible(true);
      this.pencil2.setPosition(d_medium.x + 5,d_medium.y - 5);
    }).setVisible(false)
    .on('pointerout', () => this.pencil2.setVisible(false));

    
    const d_hard = this.add.text(
      250,
      190,
      'Hard',
      {
        fontFamily: 'Gloria Hallelujah ',
        fontSize: '20px',
        fill: '#fff',
        shadow: {
          color: '#000', fill:true, offsetY:2, offsetX: 2
        },
        padding: {right: 10, up: 5}})
    .on('pointerdown', () => {
        this.difficulty = 'hard';
        this.pencilAnim(d_hard,this.pencil2);
        this.exitMenu('freeplayScene',500);
    }).setInteractive()
    .on('pointerover', () => {
      this.pencil2.setVisible(true);
      this.pencil2.setPosition(d_hard.x + 5,d_hard.y - 5);
    }).setVisible(false)
    .on('pointerout', () => this.pencil2.setVisible(false));

    const difficulty_options = this.add.group().setDepth(1);
    difficulty_options.addMultiple([d_easy,d_medium,d_hard]);


    const freeplay_button = this.add.text(
        90,
        150,
        'Freeplay',
        {
            fontFamily: 'Gloria Hallelujah ',
            fontSize: '30px',
            fill: '#fff',
            shadow: {
              color: '#000', fill:true, offsetY: 3, offsetX: 3
            },
            padding: {right: 10}}
    ).setInteractive()
    .on('pointerdown', () => {
      difficulty_options.setVisible(true);
      this.pencilAnim(freeplay_button,this.pencil);
    })
    .on('pointerover', () => {
      this.pencil.setVisible(true);
      this.pencil.setPosition(freeplay_button.x + 5,freeplay_button.y - 5);
    })
    .on('pointerout', () => {
      this.pencil.setVisible(false);
    });
    

    const tutorial_button = this.add.text(
        90,
        100,
        'Tutorial',
        {
          fontFamily: 'Gloria Hallelujah ',
          fontSize: '30px',
          fill: '#fff',
          shadow: {
            color: '#000', fill:true, offsetY: 3, offsetX: 3
          },
          padding: {right: 10}}
    ).setInteractive()
    .on('pointerdown', () => {
      this.pencilAnim(tutorial_button,this.pencil)
      this.time.delayedCall(500,this.exitMenu('tutorialScene',500));
    })
    .on('pointerover', () => {
      this.pencil.setVisible(true);
      this.startDrawing = false;
      difficulty_options.setVisible(false);
      this.pencil.setPosition(tutorial_button.x + 5, tutorial_button.y-5);
    })
    .on('pointerout', () => {
      this.pencil.setVisible(false);
      
    });
    
    const theory_button = this.add.text(
        90,
        200,
        'Theory',
        {
          fontFamily: 'Gloria Hallelujah ',
          fontSize: '30px',
          fill: '#fff',
          shadow: {
          color: '#000', fill:true, offsetY: 3, offsetX: 3
        },
        padding: {right: 10}}
    ).setInteractive()
    .on('pointerdown', () => {
      this.pencilAnim(theory_button,this.pencil)
      this.time.delayedCall(500,this.exitMenu('theoryScene',500));
    })
    .on('pointerover', () => {
      this.pencil.setVisible(true);
      this.startDrawing = false;
      difficulty_options.setVisible(false);
      this.pencil.setPosition(theory_button.x + 5,theory_button.y - 5);
    })
    .on('pointerout', () => this.pencil.setVisible(false));
    
    this.events.emit("scene-awake");

  }

  pencilAnim(button,pencil) {
    //Add pencil strikethrough
   
   
    if(this.difficulty != 'null') {
      this.pencil2_strike = new Phaser.Curves.Line([ button.x, button.y+18, pencil.x, button.y+18 ]);
      this.startDrawing2 = true;
    } else {
      this.pencil1_strike = new Phaser.Curves.Line([ button.x, button.y+33, pencil.x, button.y+33 ]);
      this.startDrawing = true;
    }
    var pencilDuration = 200;
   // 
  
    
    this.tweens.add({
      targets:  pencil,
      x: button.width + pencil.x + 30,
      duration: pencilDuration
    });

  }

  exitMenu(newScene,duration){
    this.input.enabled = false;
    this.cameras.main.fadeOut(duration,0,0,0);
    this.time.delayedCall(duration, () => {
      this.scene.start(newScene, this.difficulty);
    });
  }

  update(time, delta) {

      this.graphics.clear();
      this.graphics.lineStyle(5,0x00000,1);
      if(this.startDrawing && this.pencil.x>90){
        this.pencil1_strike.p1.x = this.pencil.x - 30;
        this.pencil1_strike.draw(this.graphics);
      }
      if(this.startDrawing2 & this.pencil2.x>230){
        this.pencil2_strike.p1.x = this.pencil2.x - 30;
        this.pencil2_strike.draw(this.graphics);
      }
  }

  addFloatingGadgets(){
     //Animations
     var shadowPlugin = this.plugins.start('rexdropshadowpipelineplugin');
     this.anims.create({
       key: "modemAnim",
       frames: this.anims.generateFrameNames("mm_modem", {
         prefix: 'anim',
         start: 1,
         end: 3
       }),
       frameRate: 2,
       repeat: -1
     })
 
     this.anims.create({
       key: "phone1Anim",
       frames: this.anims.generateFrameNames("mm_phone1", {
         prefix: 'doodlejump',
         start: 1,
         end: 12
       }),
       frameRate: 6,
       repeat: -1
     })
 
     this.anims.create({
       key: "phone2Anim",
       frames: this.anims.generateFrameNames("mm_phone2", {
         prefix: 'text',
         start: 1,
         end: 6
       }),
       frameRate: 2,
       repeat: -1
     })
 
     this.anims.create({
       key: "laptopAnim",
       frames: this.anims.generateFrameNames("mm_laptop", {
         prefix: 'dvd',
         start: 1,
         end: 30,
       }),
       frameRate: 6,
       repeat: -1
     })
 
     this.anims.create({
       key: "tabletAnim",
       frames: this.anims.generateFrameNames("mm_tablet", {
         prefix: 'angrybird',
         start: 1,
         end: 10
       }),
       frameRate: 5,
       repeat: -1
     })
 
 
     //Add floating UI elements
     var laptop = this.add.sprite(400,480,"mm_laptop","default").setInteractive()
     .on('pointerover', () => {
       laptop.anims.play('laptopAnim');
     })
     .on('pointerout', () => {
       laptop.anims.stop('laptopAnim');
       laptop.setFrame("default");
 
     })
     new FloatAnim(laptop);
     var modem = this.add.sprite(700,150,"mm_modem","default").setInteractive()
     .on('pointerover', () => {
         modem.anims.play('modemAnim');
     })
     .on('pointerout', () => {
       modem.anims.stop('modemAnim');
       modem.setFrame("default");
 
     })
     new FloatAnim(modem);
     var phone1 = this.add.sprite(580,430,"mm_phone1","default").setInteractive()
     .on('pointerover', () => {
       phone1.anims.play('phone1Anim');
     })
     .on('pointerout', () => {
       phone1.anims.stop('phone1Anim');
       phone1.setFrame("default");
 
     })
     new FloatAnim(phone1);
     var phone2 = this.add.sprite(160,360,"mm_phone2","default").setInteractive()
     .on('pointerover', () => {
       phone2.anims.play('phone2Anim');
     })
     .on('pointerout', () => {
       phone2.anims.stop('phone2Anim');
       phone2.setFrame("default");
 
     })
     new FloatAnim(phone2);
     var tablet = this.add.sprite(470,260,"mm_tablet","default").setInteractive()
     .on('pointerover', () => {
       tablet.anims.play('tabletAnim');
     })
     .on('pointerout', () => {
       tablet.anims.stop('tabletAnim');
       tablet.setFrame("default");
 
     })
     new FloatAnim(tablet);
 
     //Shadow Plugin
     shadowPlugin.add(laptop, {
       distance: 30,
       angle: -90,
 
       shadowColor: 0x1f7086,
       alpha: 1.0
     });
     shadowPlugin.add(modem, {
       distance: 30,
       angle: -90,
 
       shadowColor: 0x1f7086,
       alpha: 1.0
     });
     shadowPlugin.add(phone1, {
       distance: 30,
       angle: -90,
 
       shadowColor: 0x1f7086,
       alpha: 1.0
     });
     shadowPlugin.add(tablet, {
       distance: 30,
       angle: -90,
 
       shadowColor: 0x1f7086,
       alpha: 1.0
     });
     shadowPlugin.add(phone2, {
       distance: 30,
       angle: -90,
 
       shadowColor: 0x1f7086,
       alpha: 1.0
     });

  }
}
export default Menu;