import emitter from "./eventDispatcher.js";

export default class userEquipment extends Phaser.GameObjects.Container{

    constructor(scene,x,y,key){
        super(scene,x,y);

        scene.add.existing(this);
        this.mainSprite = scene.add.sprite(0,0,key);
        this.graphics = scene.add.graphics().setDepth(2);
        this.connectionIndictator = scene.add.sprite(0,0,'device_bars','default').setScale(0.1);
        this.userText = scene.add.text(0,40, '0%', { fontSize: '16px',fill: '#000', fontFamily: 'Gloria Hallelujah '}); 
        this.tLine = new Phaser.Curves.Line([ 0, 0, 0, 0 ]);  
        this.envelopeGroup = scene.add.container(0,0).setVisible(false); //Idk why this needed to be a container
        this.add([this.mainSprite,this.graphics,this.userText,this.connectionIndictator,this.envelopeGroup]);
        this.cameraZoom = scene.cameras.main.zoom;
        this.lastSelected = 'null';
        //this.setRotation(Phaser.Math.RND.between(-15,15))

        //Data on base station or relay selected :)
        this.bsData = {name: 'null', range: 0, coords: {x: 0, y: 0}, selected: false};
        this.relayData = {name: 'null', range: 0, coords: {x: 0, y: 0}, selected: false};
        //Unique Properties
        this.bars = 3;
        this.successCount = 0;
        this.begun = false;
        this.tethered = false; 
        //UI, Animations & Listeners
        this.on('pointerdown', this.handlePointerDown,this);
         //Base station selected
        emitter.on('bs_selected',(eventData) => {

            console.log('bs_selected');
            if(this.begun) return; //Transmission has already started.
            this.lastSelected = 'bs';
            //Update Base Station Properties
            this.bsData.selected = true;
            this.frequency = eventData.f; 
            this.freqType = eventData.t;
            this.bsData.name = eventData.name;
            this.bsData.range = eventData.range;
            this.bsData.coords = {x: eventData.x,y:eventData.y};

            //Update transmission properties
            const localCoords = this.getLocalPoint(this.bsData.coords.x,this.bsData.coords.y)
            this.tLine.p0.x = localCoords.x
            this.tLine.p0.y = localCoords.y
            this.distance = Phaser.Math.Distance.Between(this.x,this.y,this.bsData.coords.x,this.bsData.coords.y);
            this.speed =  Math.min(this.distance/60,8);
            this.duration = 3000 + 3 ** this.speed;
            console.log(this.distance);
            if(this.duration > 7000){
                this.bars = 1;
            } else if(this.duration > 4000){
                this.bars = 2;
            } else {
                this.bars = 3;
            }

            
        })

        emitter.on('relay_selected', (eventData) => {
            console.log('relay_selected'); 
            if(this.begun) return;//Transmission has already started.
            this.lastSelected = 'relay';

            //Update Relay Properties
            this.relayData.selected = true;
            this.frequency = eventData.f; 
            this.freqType = eventData.t;
            this.relayData.name = eventData.name;
            this.relayData.range = eventData.range;
            this.relayData.coords = {x: eventData.x,y:eventData.y};

            //Update transmission properties
            const localCoords = this.getLocalPoint(this.relayData.coords.x,this.relayData.coords.y)
            this.tLine.p0.x = localCoords.x
            this.tLine.p0.y = localCoords.y

            this.distance = Phaser.Math.Distance.Between(this.x,this.y,this.relayData.coords.x,this.relayData.coords.y);
            console.log(this.distance);
            this.speed =  Math.min(this.distance/60,8);
            this.duration = 3000 + 3 ** this.speed;

            if(this.duration > 7000){
                this.bars = 1;
            } else if(this.duration > 4000){
                this.bars = 2;
            } else {
                this.bars = 3;
            }

            
        });

        //Base station unselected
        emitter.on('bs_unselected', (name) => {
            if(this.bsData.name == name) this.bsData.selected = false;
        })

        emitter.on('relay_unselected', (name) => {
            if(this.relayData.name == name) this.relayData.selected = false;

        });

    }

    handlePointerDown(pointer){
        if(pointer.rightButtonDown()) return;

        console.log(this.lastSelected);
        if(this.lastSelected == 'bs'){
            console.log(this.distance);
            if(this.distance < this.bsData.range){
                this.tethered = this.bsData.selected;
                if(this.tethered) {
                    emitter.emit('user_started',{f: this.frequency, name: this.bsData.name});
                    emitter.emit('bs_unselected',this.bsData.name);
                    this.handleTransmission(); 
                }
            }
        } 
        else if (this.lastSelected == 'relay'){
            if(this.distance < this.relayData.range){
                this.tethered = this.relayData.selected;
                if(this.tethered) {
                    emitter.emit('user_started',{f: this.frequency, name: this.relayData.name});
                    emitter.emit('relay_unselected',this.relayData.name);
                    this.handleTransmission();
                }
            }
        }   
    }

    handleTransmission(){
        [this.boxMove, this.paths] = this.newBoxAnim(this.scene,this.handleEnvelope,this.successHandler);
        this.connectionIndictator.setFrame(this.freqType + '_' + this.bars);
        this.envelopeGroup.setVisible(true);
        this.disableInteractive();
    }

    newBoxAnim(scene,handleComplete,handleSuccess) {
        var boxMove = scene.tweens.createTimeline({onComplete: handleSuccess, onCompleteScope:this});
        var paths = [];
        //var animTime = Math.round(this.duration * this.speed);
        for(var i = 0; i < this.envCount; i++){
            var temp_envelope = scene.add.sprite(-400,-400,'hd_envelope').setScale(0.2)
            .setVisible(true);
            var temp_path = {t: 0, vec: new Phaser.Math.Vector2()};
            paths.push(temp_path)
            this.envelopeGroup.add(temp_envelope);
            boxMove.add({targets: paths[i],
                t: 1,
                ease: 'linear',
                duration: this.duration,
                offset: 500*i,
                onComplete: handleComplete,
                repeat: 0,
                onCompleteScope: this
            });
        }
        return [boxMove, paths];

    }
    
    handleEnvelope() {
        emitter.emit('add_score',1);
        this.envelopeGroup.list[this.successCount].setVisible(false);
        this.successCount += 1;
        //this.envCount -= 1; // Decrements envelopes by 1 for each successful data packet
        this.userText.setText(Phaser.Math.CeilTo(Phaser.Math.Percent(this.successCount,0,this.envCount)*100) + '%'); // Update the packetCount
        if(this.envCount == this.successCount){
            this.boxMove.emit('Success');
        }
    }

    boxAnim() {
        this.begun = true; 
        this.graphics.lineStyle(5, this.frequency, 1); //Red Line for Red Freq.
        this.tLine.draw(this.graphics);
        this.boxMove.play();
        for(var i = 0;i<this.paths.length;i++){
            this.tLine.getPoint(this.paths[i].t, this.paths[i].vec);
            var worldPoint = this.scene.cameras.main.getWorldPoint(this.paths[i].vec.x, this.paths[i].vec.y);
            this.envelopeGroup.list[i].setPosition(worldPoint.x*this.cameraZoom, worldPoint.y*this.cameraZoom);
            //this.graphics.fillRect(this.paths[i].vec.x - 8, this.paths[i].vec.y - 8, 16, 16);
        }
    }

    update(){
        this.graphics.clear();
        if(this.begun) this.disableInteractive();
        if(typeof this.boxMove != "undefined") {
            if(this.tethered || this.begun){
                this.boxAnim();
            } 
        }
    }

    
    successHandler(scene) {
        this.envelopeGroup.setVisible(false);
        this.boxMove.stop();
        //emitter.emit('add_score',4);
        if(this.lastSelected == 'relay'){
            emitter.emit('user_finished',{f: this.frequency, name: this.relayData.name});
        } else {
            emitter.emit('user_finished',{f: this.frequency, name: this.bsData.name}); //This informs the Base Station that the user has finished.
        }
        emitter.emit('userFinished'); // This updates the scene to spawn new entities.
        this.cleanup();
    }

    cleanup(){
        this.graphics.clear();
        this.setActive(false);
        this.setVisible(false);
        this.scene.active = false;
        //this.userText.destroy();
        //this.destroy();
    }
}
