import emitter from "./eventDispatcher.js"

export default class baseEquipment extends Phaser.GameObjects.Container {

    constructor(scene,x,y,key){
        super(scene,x,y);
        scene.add.existing(this);
        this.mainSprite = scene.add.sprite(10,0,key);
        this.graphics = scene.add.graphics();
        this.add([this.mainSprite,this.graphics]);
        this.frequency = 0xec1c24; //Frequency for communication, default to red
        this.freqType = 'red'; //This makes navigating atlas related to frequency easier:)
        scene.input.on('pointermove',this.drawLineToPointer,this);

        this.hover = false; //Is mouse hovering, might not be needed to initalise

        this.toggled = false; //This is how we tell if this BS is selected
        this.mainSprite.on('pointerover', () => {
            this.hover = true;
        });
        this.mainSprite.on('pointerout', () => {
            this.hover = false
        });
    }

    drawLineToPointer() {
        this.graphics.clear();
        this.cursorLocation = this.scene.cameras.main.getWorldPoint(this.scene.input.activePointer.x, this.scene.input.activePointer.y);
        const zoom = this.scene.cameras.main.zoom;
        this.worldPoint = {x: (this.cursorLocation.x-this.x), y:(this.cursorLocation.y - this.y)}
        if(this.toggled && this.rangeCircle.contains(this.worldPoint.x,this.worldPoint.y)){
            this.graphics.lineStyle(5, this.frequency, 1.0);
            this.graphics.beginPath();
            var headCoords = this.getLocalPoint(this.x+16,this.y-50);
            this.graphics.moveTo(headCoords.x, headCoords.y);
            this.graphics.lineTo(this.worldPoint.x, this.worldPoint.y);
            this.graphics.closePath();
            this.graphics.strokePath();
        }
      }
      
      
      update(){
        if(this.toggled){
            this.graphics.lineStyle(2,0xCCCCCC);
            this.graphics.strokeCircle(0,0,this.range);
        } else {
            this.graphics.clear();
        }
      }
}
