import emitter from "./eventDispatcher.js"
import userEquipment from "./userEquipment.js"

export default class Car extends userEquipment {

    constructor(scene,x,y){
        super(scene,x,y,'hd_car');

        this.mainSprite.setScale(0.4);
        this.setSize(this.mainSprite.displayWidth,this.mainSprite.displayHeight);
        this.setInteractive();
        this.userText.setPosition(0,50);
        this.connectionIndictator.setPosition(50,-50);
        this.envCount = Phaser.Math.RND.between(3,5);
        this.progressPerc = 100/this.envCount;
       // [this.boxMove, this.paths] = this.newBoxAnim(scene,this.handleEnvelope,this.successHandler);
        this.carSpeed = 0.5;


        if(x< scene.sys.game.config.width/2) {
            this.mainSprite.setFlipX(true);
            this.mainSprite.x = this.carSpeed;
        } else this.mainSprite.x = -this.carSpeed;

        this.distance = 0;
    }

    update(){
        super.update();
        //var conCoords = this.getLocalPoint(this.x,this.y);
        this.setPosition(this.x + this.mainSprite.x, this.y);
        if(this.lastSelected =='bs'){
            var lastSelectedCoords = this.bsData.coords;
        } else {
            var lastSelectedCoords = this.relayData.coords;
        }
        var targetAdjusted = this.getLocalPoint(lastSelectedCoords.x,lastSelectedCoords.y);
        this.tLine.p0 = {x: targetAdjusted.x, y: targetAdjusted.y};
        //this.tLine.p1 = {x:conCoords.x,y: conCoords.y};
        this.distance = Phaser.Math.Distance.Between(this.x,this.y,lastSelectedCoords.x,lastSelectedCoords.y)
        if(this.x < 0 || this.x > this.scene.game.config.width/this.scene.cameras.main.zoom){
            emitter.emit('gameover',this);
        }
    }

}