import emitter from "./eventDispatcher.js";
import baseEquipment from "./baseEquipment.js";

export default class baseStation extends baseEquipment {
    constructor(scene,x,y,name){
        super(scene,x,y,'hd_baseStation');
        this.mainSprite.setScale(0.3);
        this.setSize(this.mainSprite.displayWidth,this.mainSprite.displayHeight);
        this.mainSprite.setInteractive();
        this.name = name;
        this.range = 500;
        this.rangeCircle = new Phaser.Geom.Circle(0,0,this.range);
        //Base Station Listeners for Frequency Channels
        emitter.on('new_frequency', (freqInfo) => {
            this.frequency = freqInfo.f;
            this.freqType = freqInfo.name;
        });

        emitter.on('user_started', (bsInfo) => {
            if(this.name === bsInfo.name){
                this.channelsInUse.push(bsInfo.f);
            }
            console.log(this.channelsInUse);
        })

        emitter.on('relay_to_bs', (relayInfo) => {
            if(this.name === relayInfo.name){
                if(this.channelsInUse.includes(relayInfo.f)){
                    this.channelsInUse = this.channelsInUse.filter(item => item !== relayInfo.f);
                } else {
                this.channelsInUse.push(relayInfo.f);
                }
            }
        });

        emitter.on('user_finished', (bsInfo) => {
            if(this.name === bsInfo.name){
                this.channelsInUse = this.channelsInUse.filter(item => item !== bsInfo.f);
            }
            
        });
        this.channelsInUse = [];

        scene.input.on('pointerdown',this.mouseClick,this);

    }


    mouseClick(pointer){
        if(pointer.rightButtonDown()) return;
        if(!this.toggled & this.hover) {
            this.toggled = true;
            if(!this.channelsInUse.includes(this.frequency)) {
                emitter.emit('bs_selected',{x: this.x+12, y: this.y-50, f: this.frequency, t: this.freqType, name: this.name, range: this.range});
            } 
            
        } else {
            this.graphics.clear();
            this.toggled = false;
            emitter.emit('bs_unselected',{name: this.name});
        }
    }
    
    update(){
        super.update();
    }



}