import baseStation from "../entities/baseStation.js"
import emitter from "../entities/eventDispatcher.js"
import Car from "../entities/car.js"
import Phone from "../entities/phone.js"
import relayBase from "../entities/relayBase.js"

const RED_HEX = 0xec1c24;
const BLUE_HEX = 0x3f48cc;
const GREEN_HEX = 0x0ed145;


class Tutorial extends Phaser.Scene {
    constructor() {
        super({
            key: 'tutorialScene'
        })
    }

    preload() { 
        this.cameras.main.fadeIn(1000,0,0,0);
    }

    create() {
        //Background magic
        const background = this.add.group();
        background.add(this.add.image(0,0,'paper_bg').setScale(0.5))
        background.add(this.add.image(700,0,'paper_bg').setScale(0.5));
        background.add(this.add.image(0,500,'paper_bg').setScale(0.5))
        background.add(this.add.image(700,500,'paper_bg').setScale(0.5));


        //Gameobject Reactive
        this.input.on('gameobjectdown', function (pointer, gameObject) {
            if(typeof gameObject.mainSprite != "undefined"){
                gameObject.scene.add.tween({
                    targets: gameObject.mainSprite,
                    scaleX: "*=0.8",
                    scaleY: "*=0.8",
                    duration: 120,
                    onComplete: () => {
                        gameObject.setInteractive();
                    },
                    yoyo: true
                });
                gameObject.disableInteractive();
            } else {
                gameObject.scene.add.tween({
                    targets: gameObject,
                    scaleX: "*=0.8",
                    scaleY: "*=0.8",
                    duration: 120,
                    onComplete: () => {
                        gameObject.setInteractive();
                    },
                    yoyo: true
                });
                gameObject.disableInteractive();
            }
        });

        const frequency_buttons = this.add.group();
        //Base Station UI Control
        var red_button = this.add.sprite(350, this.game.config.height-50, 'buttons','redbutton')
        .setDepth(1)
        .setScale(0.6)
        .setVisible(false)
        //red_button.setSize(red_button.displayWidth,red_button.displayWidth)
        .setInteractive()
        .on('pointerdown', () => {
            emitter.emit('new_frequency',{f: RED_HEX, name: 'red'});
            emitter.emit('bs_unselected', 'bs_1');
        })

        var blue_button = this.add.sprite(450,this.game.config.height-50, 'buttons','bluebutton')
        .setScale(0.6)
        //blue_button.setSize(blue_button.displayWidth,blue_button.displayHeight)
        .setDepth(1)
        .setInteractive()
        .setVisible(false)
        .on('pointerdown', () => {
            emitter.emit('new_frequency',{f: BLUE_HEX, name: 'blue'});
            emitter.emit('bs_unselected', 'bs_1');
        });

        frequency_buttons.addMultiple([red_button,blue_button])
        this.progressCount = 0;
        this.usersFinished = 0;
        this.notFirst = true;
        this.allDevices = this.add.group({runChildUpdate: true});
        this.progress = true;
        this.txtScene = this.scene.get('textBoxScene');
        //Emitters
        emitter.on('user_started', () => {
            if(this.usersFinished == 0){
                this.progress = true;
                this.events.emit('Progress');
            } else if(this.usersFinished == 1) {
                this.progress = true;
                this.events.emit('Progress')
            }
        });

        emitter.on('userFinished', () => {
            this.usersFinished += 1;
            console.log(this.usersFinished);
            if(this.usersFinished == 1){
                if(this.progressCount < 6) this.progressCount = 6;
                this.progress = true;
                console.log(this.progressCount);
                this.events.emit('Progress');
            } else if(this.usersFinished == 2) {
                if(this.progressCount < 8) this.progressCount = 8;
                this.progress = true;
                this.events.emit('Progress');
            } else if(this.usersFinished == 3){
                if(this.progressCount < 9) this.progressCount = 9;
                this.progress = true;
                this.events.emit('Progress');
            } else if(this.usersFinished == 5){
                this.progress = true;
                this.events.emit('Progress');
            }
        });


        //textBox.on('pointerdown', () => {textBox.setVisible(false);});
        this.events.on('Progress', () => {
            console.log(this.progressCount, this.progress)
            switch(this.progressCount){
                case 1:    
                    this.mainTower = new baseStation(this,400,150,'bs').setScale(2)
                    this.txtScene.handleTextBoxUpdate(1);
                    break;
                case 2: 
                    var tween = this.tweens.add({
                        targets: this.mainTower,
                        y: '+=30', // '+=100'
                        x: '-=250',
                        scale: 1,
                        ease: 'Linear', // 'Cubic', 'Elastic', 'Bounce', 'Back'
                        duration: 500,
                        repeat: 0, // -1: infinity
                        scope: this
                    });
                    this,this.handleTextBoxFinished();
                    break;
                case 3: 
                    this.phone_1 = new Phone(this,400,150,this.add_score,10000).setScale(2);
                    this.allDevices.add(this.phone_1);
                    this.txtScene.handleTextBoxUpdate(2);
                    break;
                case 4: 
                    var tween = this.tweens.add({
                        targets: this.phone_1,
                        y: '+=70', // '+=100'
                        x: '-=150',
                        scale: 1,
                        ease: 'Linear', // 'Cubic', 'Elastic', 'Bounce', 'Back'
                        duration: 500,
                        repeat: 0, // -1: infinity
                        scope: this
                    }); 
                    this.txtScene.handleTextBoxUpdate(3);
                    this.progress = false; 
                    break;
                case 5: 
                    if(this.progress) { //Waiting for player to connect BS to User
                        this.txtScene.handleTextBoxUpdate(4);
                        this.progress = false;
                    }
                    break;
                case 6: 
                    if(this.progress) { //Waiting for phone_1 to finish
                        this.txtScene.handleTextBoxUpdate(5);
                        this.phone_2 = new Phone(this,600,300,this.add_score,10000).setDepth(5);
                        this.allDevices.add(this.phone_2);
                        this.progress = false;
                    }
                    break;
                case 7:
                    if(this.progress) { //Waiting for phone_2 to start
                        this.txtScene.handleTextBoxUpdate(6);
                        this.progress = false;
                    }
                    break;
                case 8:
                    if(this.progress) { //Waiting for phone_2 to finish
                        this.relay_1 = new relayBase(this,400,300,'relay').setScale(2);
                        this.allDevices.add(this.relay_1);
                        this.txtScene.handleTextBoxUpdate(7);
                        this.progress = false;
                    }
                    break;
                case 9:
                    var tween = this.tweens.add({
                        targets: this.relay_1,
                        y: '+=30', // '+=100'
                        x: '-=30',
                        scale: 1,
                        ease: 'Linear', // 'Cubic', 'Elastic', 'Bounce', 'Back'
                        duration: 500,
                        repeat: 0, // -1: infinity
                        scope: this
                    }); 
                    this.txtScene.handleTextBoxUpdate(8);
                    this.phone_3 = new Phone(this,600,300,this.add_score,10000).setDepth(5);
                    this.allDevices.add(this.phone_3);
                    break;

                case 10:
                    if(this.progress){
                        this.txtScene.handleTextBoxUpdate(9);
                        this.progress = false;
                    }
                    break;
                case 11: 
                    //Base Station UI Control
                    this.txtScene.handleTextBoxUpdate(10);
                    frequency_buttons.setVisible(true);
                    break;
                case 12:
                    this.txtScene.handleTextBoxUpdate(11);
                    this.phone_4 = new Phone(this,450,120,this.add_score,10000).setDepth(5);
                    this.phone_5 = new Phone(this,500,200,this.add_score,10000).setDepth(5);
                    this.allDevices.add(this.phone_4);
                    this.allDevices.add(this.phone_5);
                    break;
                case 13:
                    if(this.progress){
                        this.txtScene.handleTextBoxUpdate(12);
                    }
                    break;
                case 14:
                    this.cameras.main.fadeOut(1000,0,0,0);
                    this.time.delayedCall(1000, () => {
                        this.scene.start('menuScene');
                        this.scene.stop('textBoxScene');
                    });
                    break;
                }

        });

        this.events.once('shutdown', function() {
            this.allDevices.destroy();
            this.mainTower.destroy();
            emitter.removeAllListeners();
        }, this);
        this.scene.run('textBoxScene');
    }

    update() {}

    add_score(num){
        console.log('No scoring this time!')
    }

    handleTextBoxFinished(){
        this.input.enabled = true;
        this.progressCount += 1;
        this.events.emit('Progress')
    }
}


export default Tutorial;