import baseStation from "../entities/baseStation.js"
import emitter from "../entities/eventDispatcher.js"
import Car from "../entities/car.js"
import Phone from "../entities/phone.js"
import Laptop from "../entities/laptop.js"
import Tablet from "../entities/tablet.js"
import relayBase from "../entities/relayBase.js"

const RED_HEX = 0xec1c24;
const BLUE_HEX = 0x3f48cc;
const GREEN_HEX = 0x0ed145;
const YELLOW_HEX = 0xffec00;
const PINK_HEX = 0xa02789;
const ORANGE_HEX = 0xff8100;

export default class Freeplay extends Phaser.Scene {

    constructor(){
        super({key: 'freeplayScene'});
    }
    init(difficulty){
        this.cameras.main.fadeIn(1000,0,0,0);
        if(difficulty == 'hard'){
            this.score_threshold = 20;
            this.score_multiplier = 2;
        } else if (difficulty == 'medium') {
            this.score_threshold = 30;
            this.score_multiplier = 1;
        } else { //difficulty == easy
            this.score_threshold = 35;
            this.score_multiplier = 1;
        }
    }
    preload(){


        this.load.plugin('rexkawaseblurpipelineplugin', 'https://raw.githubusercontent.com/rexrainbow/phaser3-rex-notes/master/dist/rexkawaseblurpipelineplugin.min.js', true);

    }



    create(){
        //Background magic
        const background = this.add.group();
        background.add(this.add.image(0,0,'paper_bg').setScale(0.5));
        background.add(this.add.image(700,0,'paper_bg').setScale(0.5));
        background.add(this.add.image(0,500,'paper_bg').setScale(0.5));
        background.add(this.add.image(700,500,'paper_bg').setScale(0.5));
        background.add(this.add.image(1400,0,'paper_bg').setScale(0.5))
        background.add(this.add.image(2100,0,'paper_bg').setScale(0.5));
        background.add(this.add.image(1400,500,'paper_bg').setScale(0.5));
        background.add(this.add.image(2100,500,'paper_bg').setScale(0.5));
        background.add(this.add.image(0,1000,'paper_bg').setScale(0.5));
        background.add(this.add.image(700,1000,'paper_bg').setScale(0.5));
        background.add(this.add.image(1400,1000,'paper_bg').setScale(0.5))
        background.add(this.add.image(2100,1000,'paper_bg').setScale(0.5));
        background.add(this.add.image(0,1500,'paper_bg').setScale(0.5));
        background.add(this.add.image(700,1500,'paper_bg').setScale(0.5));
        background.add(this.add.image(1400,1500,'paper_bg').setScale(0.5));
        background.add(this.add.image(2100,1500,'paper_bg').setScale(0.5));
        
        //Camera Control
        this.fadePlugin = this.plugins.start('rexkawaseblurpipelineplugin');
        this.input.setDefaultCursor('pencil_cursor, pointer');
        this.score = 0;
        this.newZoom = 1.0;
        this.zoomDuration = 10000;
        this.cameraThreshold = this.score_threshold + 5;
        this.buttonThreshold = 100;
        const cam = this.cameras.main;
        cam.setBounds(0,0,this.game.config.width,this.game.config.height);
        const UIcam = this.cameras.add(0,0,this.game.config.width,this.game.config.height).setName("UIcam");
        const pauseCam = this.cameras.add(0,0,this.game.config.width,this.game.config.height).setName("pauseCam");

        emitter.on('camera_update', () =>{
            this.newZoom -= 0.1;
            cam.zoomTo(this.newZoom, this.zoomDuration, 'Linear', true, (camera,progress) => {
                this.UE_group.getChildren().forEach((child) => {
                    child.cameraZoom = camera.zoom;
                });
                const viewportWidth = camera.width;
                const viewportHeight = camera.height;
                const centerX = this.game.config.width/2// * camera.zoom;
                const centerY = this.game.config.height/2 //* camera.zoom;
                const cameraX = centerX - viewportWidth / 2;
                const cameraY = centerY - viewportHeight / 2;
                camera.centerOn(cameraX, cameraY);
            });
            var new_relay = new relayBase(this,Phaser.Math.RND.between(100,700),Phaser.Math.RND.between(100,500),'relay_' + this.RS_group.countActive() + 1);
            this.RS_group.add(new_relay);
            UIcam.ignore(this.RS_group); pauseCam.ignore(this.RS_group);

        });




        //Gameobject Reactive
        this.input.on('gameobjectdown', function (pointer, gameObject) {
            if(typeof gameObject.mainSprite != "undefined"){
                gameObject.scene.add.tween({
                    targets: gameObject.mainSprite,
                    scaleX: "*=0.8",
                    scaleY: "*=0.8",
                    duration: 120,
                    onComplete: () => {
                        gameObject.setInteractive();
                    },
                    yoyo: true
                });
                gameObject.disableInteractive();
            } else {
                gameObject.scene.add.tween({
                    targets: gameObject,
                    scaleX: "*=0.8",
                    scaleY: "*=0.8",
                    duration: 120,
                    onComplete: () => {
                        gameObject.setInteractive();
                    },
                    yoyo: true
                });
                gameObject.disableInteractive();
            }
        });
            
        //Group Setup

        this.UE_group = this.add.group({runChildUpdate: true}).setDepth(1);
        this.RS_group = this.add.group({runChildUpdate: true}).setDepth(2);
        this.BS_group = this.add.group({runChildUpdate: true}).setDepth(3);

        emitter.on('userFinished', (bsInfo) => {
            var carCoords = [25, 775/cam.zoom];
            this.maxUsers = 1// + Phaser.Math.FloorTo(this.score/this.score_threshold);
            for(var i = 0; i < this.maxUsers; i++){
                var newUser = 0;
                if(this.UE_group.countActive()>this.maxUsers) break;
                switch(Phaser.Math.RND.between(1,4)){ //1 to Types of Devices
                    case 1: //Default: Mobile Phone
                        newUser = new Phone(this,Phaser.Math.RND.between(150,750/cam.zoom),Phaser.Math.RND.between(50,500/cam.zoom)).setDepth(5);
                        break;
                    case 2: //Car Class, moves across screen but changes
                        newUser = new Car(this,carCoords[Phaser.Math.RND.between(0,1)],Phaser.Math.RND.between(100,550/cam.zoom)).setDepth(5);
                        break;
                    case 3://Laptop?
                        newUser = new Laptop(this,Phaser.Math.RND.between(150,750/cam.zoom),Phaser.Math.RND.between(50,500/cam.zoom)).setDepth(5);
                        break;
                    case 4://Tablet
                        newUser = new Tablet(this,Phaser.Math.RND.between(150,750/cam.zoom),Phaser.Math.RND.between(50,500/cam.zoom)).setDepth(5);
                        break;
                }
                this.UE_group.add(newUser);
            }
            UIcam.ignore(this.UE_group); pauseCam.ignore(this.UE_group);
        })
        this.scoreText = this.add.text(10, -10, 'Score: ' + this.score, { fontSize: '40px', fill: '#000', fontFamily: 'Gloria Hallelujah' });
        //Add all towers
        this.mainTower = new baseStation(this,Phaser.Math.RND.between(300,375),300,'bs_1').setDepth(1);
        this.secondTower = new baseStation(this,1000,1000,'bs_2').setDepth(1);

        var relayOne = new relayBase(this,450,450,'relay_1').setDepth(1);
        console.log('boop');
        this.RS_group.add(relayOne);
        this.BS_group.add(this.mainTower);
        this.BS_group.add(this.secondTower);
        console.log('boop');
      
        emitter.emit('userFinished');
        emitter.on('gameover', (failUE) => {
            console.log(failUE); //Could show the user this?
            this.blur_cameras(cam,UIcam,4);
            this.scene.pause();
            this.scene.launch('gameoverScene',{finalScore: this.score});
        }, this)
            
        //UI Buttons-------------------------------------------------------------------------------------
        var pause_button = this.add.sprite(this.game.config.width-50,30, 'shapes','pause_button').setInteractive()
        .on('pointerdown', () => {
            this.blur_cameras(cam,UIcam,4);
            pause_button.setVisible(false);
            this.scene.pause();
            this.scene.launch('pauseScene');
        })
        .setScale(0.5)
        .setAlpha(1);
        emitter.on('add_score', (num) => {
            this.score += num * this.score_multiplier;
            this.scoreText.setText('Score: ' + this.score);

            if(this.score > this.cameraThreshold){
                this.zoomDuration *= 2;
                this.cameraThreshold *= 2;
                emitter.emit('camera_update');
            }

            if(this.score > this.buttonThreshold){
                this.buttonThreshold += 100;
                if(this.buttonThreshold== 200) {
                    yellow_button.setVisible(true);
                    this.adjust_buttons(frequency_buttons);
                } else if(this.buttonThreshold == 300){
                    pink_button.setVisible(true);
                    this.adjust_buttons(frequency_buttons);
                } else if(this.buttonThreshold == 500) {
                    orange_button.setVisible(true);
                    this.adjust_buttons(frequency_buttons);
                }
                cam.ignore(frequency_buttons);
            }
        });
        const frequency_buttons = this.add.group();
        //Base Station UI Control
        var red_button = this.add.sprite(350, this.game.config.height-50, 'buttons','redbutton')
        .setDepth(1)
        .setScale(0.6)
        //red_button.setSize(red_button.displayWidth,red_button.displayWidth)
        .setInteractive()
        .on('pointerdown', () => {
            emitter.emit('new_frequency',{f: RED_HEX, name: 'red'});
            emitter.emit('bs_unselected', 'bs_1');
        })

        var green_button = this.add.sprite(450,this.game.config.height-50, 'buttons','greenbutton')
        .setScale(0.6)
        //green_button.setSize(green_button.displayWidth,green_button.displayHeight)
        .setDepth(1)
        .setInteractive()
        .on('pointerdown', () => {
            emitter.emit('new_frequency',{f: GREEN_HEX, name: 'green'});
            emitter.emit('bs_unselected', 'bs_1');
        });

        var blue_button = this.add.sprite(550,this.game.config.height-50, 'buttons','bluebutton')
        .setScale(0.6)
        //blue_button.setSize(blue_button.displayWidth,blue_button.displayHeight)
        .setDepth(1)
        .setInteractive()
        .on('pointerdown', () => {
            emitter.emit('new_frequency',{f: BLUE_HEX, name: 'blue'});
            emitter.emit('bs_unselected', 'bs_1');
        });

        var yellow_button = this.add.sprite(650,this.game.config.height-50, 'buttons','yellowbutton')
        .setScale(0.6)
        //yellow_button.setSize(yellow_button.displayWidth,yellow_button.displayHeight)
        .setDepth(1)
        .setInteractive()
        .setVisible(false)
        .on('pointerdown', () => {
            emitter.emit('new_frequency', {f: YELLOW_HEX, name: 'yellow'});
            emitter.emit('bs_unselected', 'bs_1');
        });

        var pink_button = this.add.sprite(750,this.game.config.height-50, 'buttons','pinkbutton')
        .setScale(0.6)
       // pink_button.setSize(pink_button.displayWidth,pink_button.displayHeight)
        .setDepth(1)
        .setInteractive()
        .setVisible(false)
        .on('pointerdown', () => {
            emitter.emit('new_frequency', {f: PINK_HEX, name: 'pink'});
            emitter.emit('bs_unselected', 'bs_1');
        });

        var orange_button = this.add.sprite(850,this.game.config.height-50, 'buttons','orangebutton')
        .setScale(0.6)
        //orange_button.setSize(orange_button.displayWidth,orange_button.displayHeight)
        .setDepth(1)
        .setInteractive()
        .setVisible(false)
        .on('pointerdown', () => {
            emitter.emit('new_frequency', {f: ORANGE_HEX, name: 'orange'});
            emitter.emit('bs_unselected', 'bs_1');
        });

        frequency_buttons.addMultiple([red_button,blue_button,green_button,yellow_button,pink_button,orange_button]);
        this.adjust_buttons(frequency_buttons);
        this.events.on('resume', () => {
            this.fadePlugin.remove(cam);
            this.fadePlugin.remove(UIcam);
            pause_button.setVisible(true);
        });
        this.events.once('shutdown', function() {
            this.UE_group.destroy();
            this.mainTower.destroy();
            this.fadePlugin.stop();
            emitter.removeAllListeners();
        }, this);


        cam.ignore([this.scoreText,frequency_buttons,pause_button]);
        UIcam.ignore([this.BS_group,this.RS_group,this.UE_group,pause_button,background]);
        pauseCam.ignore([this.scoreText,frequency_buttons,this.BS_group,this.UE_group,this.RS_group,background]);
    }

//UPDATE FUNCTION
    update(time,delta){
    }

    adjust_buttons(buttons){
        buttons.getChildren().forEach(function(sprite) {
            sprite.x -= 50;
          }, this);
    }

    blur_cameras(cam,UIcam,b){
        this.fadePlugin.add(cam, {blur: b})
        this.fadePlugin.add(UIcam, {blur: b});
    }
}




